const TOOLTIP_CLASS = "k-chart-tooltip";

/**
 * @hidden
 */
export abstract class BaseTooltip {
  protected active: boolean = false;
  protected offset: any = {};
  protected align: any = {};
  protected style: any = {};
  protected popupClasses: any = {};
  protected collision: any = { horizontal: "fit", vertical: "fit" };


  public show(e: any): void {
    this.active = true;
    this.align = e.anchor.align;
    this.offset = e.anchor.point;
    this.popupClasses = {
        [ TOOLTIP_CLASS ]: true,
        [ e.className ]: !!e.className
    };
    this.style = e.style;
  }

  public hide(): void {
    this.active = false;
  }
}



